.App {
  padding: 2% 5%;
  display: flex;
  gap: 1vh;
  flex-direction: column;
}
.refresh>button {
  border: none;
  padding: 1% 2%;
  font-size: 1.2rem;
  font-weight: 800;
  border-radius: 8px;
}
.refresh {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: sticky;
  top:0;
  width: 100%;
  height: fit-content;
  background: white;
}