.videoPage {
  padding: 2% 5%;
  padding-top: 1%;
}
.videoTitle {
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  padding: 2% 0;
}
.player {
  display: flex;
  width: 100%;
  justify-content: center;
}
.channel{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2ch;
}
.channelTitle{
  flex: 1;
  font-weight: 600;
  font-size: 1.2rem;
  position: relative;
}
.channelSubs {
  color: red;
}
.channelSubs::before{
  content: 'Subscribers :';
  color: black;
}
.channelIcon>img {
  border-radius: 50%;
}
.videoDesc {
  padding: 2% 0;
}
.views::before {
  content: 'Views: ';
}
.likes::before {
  content: 'Likes üëç :';
  padding-right: 1ch;
}
.meta {
  display: flex;
  justify-content: space-between;
  padding: 2% 0;
}
.views{
  font-weight: 600;
}
.likes {
  font-weight: 600;
}
.half {
  height: 10ch;
  overflow: hidden;
}
.toggleDesc {
  outline: none;
}